header {
    background-color: rgb(249,219,121);
}

.topo div {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.link {
    margin: 25px;
    text-decoration: none;
    color: #000;
    font-weight: bold;
}

.topo img {
    width: 50px;
}

@media (max-width: 436px) {

    .topo div {
        flex-direction: column;
    }

    nav {
        border-top: 1px solid grey;
        width: 90%;
        display: flex;
        justify-content: space-around;
    }

    .link {
        margin: 10px;
        font-size: 18px;
    }

}