main {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.banner-sabores {
    width: 100%;
    height: 70vh;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    background-image: url('/public/assets/banner-sabores.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.titulo-banner-sabores {
    padding: 20px 30px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.titulo-banner-sabores h1 {
    color: white;
    font-size: 100px;
    text-shadow: 3px 5px 2px #474747;
    text-transform: uppercase;
}

.secao-sabores {
    width: 100%;
    padding: 100px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.secao-sabores h2 {
    font-size: 30px;
    font-weight: 600;
}

.container-sabores {
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.card-sabores {
    width: 30%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    text-align: center;
    line-height: 26px;
    border-radius: 4px;
    box-shadow: 2px 2px 15px 2px #d9dde1;
    margin-bottom: 30px;
}

.card-sabores p {
    margin-top: 10px;
    margin-bottom: 10px;
}

.card-sabores img {
    width: 100%;
    height: 250px;
    object-fit: contain;
}

@media screen and (max-width: 768px) {
    .titulo-banner-sabores h1 {
        font-size: 60px;
    }

    .card-sabores {
        padding: 20px 40px;
        width: 100%;
        margin-bottom: 20px;
    }

    .container-sabores {
        flex-direction: column;
        align-items: center;
    }

}

@media screen and (max-width: 425px) {
    .titulo-banner-sabores h1 {
        font-size: 45px;
    }
}