footer {
    width: 100%;
    background-color: black;
}

.container-informacoes {
    width: 100%;
    display: flex;
    align-items: center;
    color: white;
    padding: 80px 20px;
    justify-content: space-between;
}

.container-informacoes img {
    width: auto;
    height: 100px;
}

.container-informacoes div {
    width: 20%;
}

.container-informacoes p {
    font-size: 16px;
}

.direito-autoral {
    width: 100%;
    color: white;
    text-align: center;
    padding: 20px;
}

@media (max-width: 436px) {

    .container-informacoes {
        flex-direction: column;
    }

    .container-informacoes div {
        width: 80%;
        text-align: center;
        margin: 20px;
    }

}

@media (max-width: 768px) {

    .container-informacoes {
        flex-direction: column;
    }

    .container-informacoes div {
        width: 80%;
        text-align: center;
        margin: 10px;
    }
}